import React from "react";
import './About.css';
import Reveal from "./Reveal";

function About(){
    return(
        <div className="about-container">
            <div className="about">
                <Reveal direction="left">
                    <img src="profile.jpg" alt="Headshot" className="pic"></img>
                </Reveal>
                <Reveal direction="right">
                    <div className="text">
                        <h2>About Me</h2>
                        <h5>Software Engineering Student</h5>
                        <p>Hey! Im a Software Engineering student whos into building cool and useful things with code.
                            Right now, Im learning a bit of everything  from front-end stuff like HTML/CSS and JavaScript to back-end tools like Java and databases. 
                            Im especially into creating apps that are actually fun or helpful to use.
                            Im the kind of person who loves figuring things out, whether its fixing a bug that wont go away or 
                            learning a new tech stack just to see what it can do.
                            Lets build something awesome!</p>
                        <div className="link">
                            <a href="resume.pdf" className="resume" target="_blank">Resume</a>
                        </div>
                    </div>
                </Reveal>
            </div>
        </div>
    )
}

export default About;